<template>
  <div id="app-wrapper" class="app-wrapper">
    <header id="app-header" class="app-header">
      <h1><a v-link="{ path: '/' }">Monine</a></h1>
      <nav>
        <a v-link="{ path: '/article' }">文章</a>
        <!-- <a v-link="{ path: '/' }">关于我</a> -->
      </nav>
    </header>
    <div id="app-container" class="app-container">
      <router-view :next-page="nextPage" :has-more-article="hasMoreArticle" :article-num="$route.params.num" @update-next-page="updateNextPage" @update-has-more-article="updateHasMoreArticle"></router-view>
    </div>
    <footer id="app-footer" class="app-footer">
      <p>© 2016 Monine</p>
    </footer>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        nextPage: 1,
        hasMoreArticle: true
      }
    },
    methods: {
      updateNextPage () {
        this.nextPage += 1
      },
      updateHasMoreArticle (bool) {
        this.hasMoreArticle = bool
      }
    }
  }
</script>

<style>
  /* base start */
  html {
    height: 100%;
    box-sizing: border-box;
    font-size: 100px;
  }
  *, *:before, *:after {
    box-sizing: inherit;
  }
  body {
    position: relative;
    height: 100%;
    margin-left: calc(100vw - 100%);
    font: 16px/1.5 "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial,sans-serif;
  }
  a {
    color: #333;
    text-decoration: none;
  }
  button {
    outline: none;
  }
  blockquote {
    padding: 0 10px;
    margin: 0;
    border-left: 5px solid #ccc;
    font-size: 14px;
    color: #333;
    line-height: 2;
  }
  pre {
    padding: 16px;
    overflow: auto;
    font-size: 12px;
    background-color: #f7f7f7;
    border-radius: 3px;
    word-wrap: break-word;
  }
  code {
    padding: 0.2em 0.5em;
    margin: 0;
    background-color: rgba(0, 0, 0, 0.04);
    border-radius: 3px;
    font: 12px Consolas, "Liberation Mono", Menlo, Courier, monospace;
  }
  pre code {
    background-color: transparent;
    padding: 0;
  }
  .hljs-built_in, .hljs-params, .hljs-number, .hljs-literal {
    color: #0086b3
  }
  .hljs-string {
    color: #183691;
  }
  .hljs-keyword, .hljs-meta, .hljs-name {
    color: #a71d5d;
  }
  .hljs-title {
    color: #795da3;
  }
  .hljs-comment {
    color: #969896;
  }
  .hljs-regexp {
    color: #183691;
  }
  .app-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
    flex-direction: column;
    width: 7rem;
    height: 100%;
    margin: auto;
  }
  /* base end */
  .app-header {
    -webkit-flex: 0 0 auto;
    flex: 0 0 auto;
    position: relative;
    padding-bottom: 0.2rem;
    border-bottom: 1px solid #999;
  }
  .app-header nav {
    font-size: 14px;
  }
  .app-header nav a {
    padding: 3px;
  }
  .app-header nav .v-link-active {
    border-bottom: 2px solid #0097da;
  }
  .app-header nav a:hover {
    border-bottom: 2px solid #0097da;
  }
  .app-container {
    -webkit-flex: 1 0 auto;
    flex: 1 0 auto;
    width: 100%;
    margin: auto;
  }
  .app-footer {
    -webkit-flex: 0 0 auto;
    flex: 0 0 auto;
    border-top: 1px solid #999;
    text-align: center;
    font-size: 12px;
    color: #999;
  }
</style>


